// bs-callout
// See: http://cpratt.co/twitter-bootstrap-callout-css-styles/
// ===============================================

.note {
  padding: 20px;
  margin: 20px 0;
  border: 1px solid #eee;
  border-left-width: 5px;
  border-radius: 3px;
}
.note h2,
.note h3,
.note h4,
.note h5,
.note h6 {
  margin-top: 0;
  margin-bottom: 5px;
  padding-top: 0 !important;
}
.note p:last-child {
  margin-bottom: 0;
}
.note code {
  border-radius: 3px;
}
.note+.note {
  margin-top: -5px;
}
.default {
  border-left-color: #777;
}
.default h2,
.default h3,
.default h4,
.default h5,
.default h6 {
  color: #777;
}
